.video-js .vjs-fullscreen-control {
  cursor: pointer;
  @include flex(none);
  @extend .vjs-icon-fullscreen-enter;
}
// Switch to the exit icon when the player is in fullscreen
.video-js.vjs-fullscreen .vjs-fullscreen-control {
  @extend .vjs-icon-fullscreen-exit;
}



